import { TweenMax, Power0 } from "gsap";

export default function flipCard(dom) {
  TweenMax.to(dom, 0.5, {
    bezier: [{
      x: 0,
      z: 0,
      rotationY: "+=0"
    }, {
      x: -500,
      z: 100,
      rotationY: "+=90"
    }, {
      x: -300,
      z: 800,
      rotationY: "+=150"
    }, {
      x: 0,
      z: 0,
      rotationY: "+=180"
    }],
    ease: Power0.easeNone
  })
}



